div.blog-featured,
div.blog,
div.items-leading,
div.items-row {
  display: block;
  overflow: hidden;
  padding: 0;
  margin: 0;
  background: transparent;
}
article {
  padding: .5em;
  margin: 0;
  display: block;
  overflow: hidden;
  background: #fefefe;
}
article.leading,
article[class^="leading-"] {
  border-bottom: 0.5em solid gainsboro;
}
article.item {
  border-bottom: 0.25em solid gainsboro;
}
article.item-page {
  background: transparent;
  padding: .5em;
  margin: 0;
}
article.item-page p.lead {
  font-size: 125% !important;
}
article p.lead {
  font-size: 112.5%;
  margin-top: 0.75em;
  margin-bottom: 1.5em;
  text-indent: 0;
  font-weight: 300;
}
article div.img_caption {
  width: auto !important;
}
article p.img_caption {
  font-style: italic;
  margin-bottom: 0.375em;
}
article div.item-image {
  height: auto;
}
article div.item-image.pull-right {
  max-width: 33.33%;
  margin-left: 1em;
  margin-bottom: 1em;
  float: right;
}
article div.item-image.pull-left {
  max-width: 33.33%;
  margin-right: 1em;
  margin-bottom: 1em;
  float: left;
}
article div.item-image.pull-none {
  width: 100%;
  margin-bottom: 1em;
}
article hr.info {
  border-top: 1px solid #AAAEB5;
  border-bottom: 0;
}
article div.info {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  float: left;
  width: 100%;
  padding: 0 0;
  border-left: 0.5em solid gainsboro;
  background: #fefefe;
  padding: 1em;
  margin-bottom: .5em;
}
article div.info:after,
article div.info:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.cols-1,
.cols-2,
.cols-3,
.cols-4 {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  float: left;
  width: 100%;
  padding: 0 0;
}
.cols-1:after,
.cols-2:after,
.cols-3:after,
.cols-4:after,
.cols-1:before,
.cols-2:before,
.cols-3:before,
.cols-4:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
div.page-header,
div.subheading-category {
  padding: 0;
  margin: 0 !important;
  border-bottom: 0 !important;
}
.item-separator {
  display: block;
  overflow: hidden;
  height: 1em;
}
.item-title {
  line-height: 1.1;
  padding: 0;
  border-bottom: 0 !important;
}
dl.article-info {
  margin: 1em 0;
  font-size: 75%;
  color: #999999;
}
.blog .category-desc {
  margin: 1em 0;
  border-bottom: 0.5em solid #e9e9e9;
}
.blog .cat-children .category-desc {
  padding: 0;
  margin: 1em 0 2em;
}
@media screen and (min-width: 47em) {
  .cols-2,
  .cols-3,
  .cols-4 {
    padding: 0 .5em;
  }
  .cols-2 {
    width: 50%;
  }
  .cols-3 {
    width: 33.3333%;
  }
  .cols-4 {
    width: 25%;
  }
