// Images - img skalierbar machen

img {
    max-width: 100%;
    height: auto;
}

img {

    &.scale-with-grid {
		max-width: 100%;
	}

	&.max-image {
		width: 100%;
	}

    &.two-thirds-image {
        width: 66%;
    }

    &.half-image {
		width: 50%;
	}

    &.third-image {
		width: 33%;
	}

	&.pull-right,
	&.right-image {
		float:right;
		display:inline;
		margin: 1.5em 0 0 1em;
	}

	&.pull-left,
	&.left-image {
		float:left;
		display:inline;
		margin: 1.5em 1em 0 0;
	}

	&.pull-center,
	&.center-image {
		display:block;
		text-align:center;
		margin: 1.5em auto 0 auto;
	}
}

// figure and figcaption (caption js)
figure {
	margin: 0 0 @paragraph-val 0;
	background: @gainsboro-lighter;
	border: 1px solid @gainsboro;

	figcaption {
		font-size: 100%;
		font-style: italic;
		margin: @paragraph-val/2 @paragraph-val;
	}

	img {
		width: 100%; // Textbreite
	}
}

// object embed and video

object, embed, canvas, iframe, video, svg {
	max-width: 100%;
}

// helper class for divs
.video-container {
	position: relative;
	margin: 0 0 @paragraph-val 0;
	padding-bottom: 51%;
	padding-top: 30px;
	height: 0;
	overflow: hidden;
	border: 1px solid @gray;

	iframe, object, embed {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

//
// instagram image filters cssgram
// add to image container > figure
.aden {
	-webkit-filter: hue-rotate(-20deg) contrast(.9) saturate(.85) brightness(1.2);
	filter: hue-rotate(-20deg) contrast(.9) saturate(.85) brightness(1.2)
}

.aden::after {
	background: -webkit-linear-gradient(left, rgba(66, 10, 14, .2), transparent);
	background: linear-gradient(to right, rgba(66, 10, 14, .2), transparent);
	mix-blend-mode: darken
}

.perpetua::after, .reyes::after {
	mix-blend-mode: soft-light;
	opacity: .5
}

.inkwell {
	-webkit-filter: sepia(.3) contrast(1.1) brightness(1.1) grayscale(1);
	filter: sepia(.3) contrast(1.1) brightness(1.1) grayscale(1)
}

.perpetua::after {
	background: -webkit-linear-gradient(top, #005b9a, #e6c13d);
	background: linear-gradient(to bottom, #005b9a, #e6c13d)
}

.reyes {
	-webkit-filter: sepia(.22) brightness(1.1) contrast(.85) saturate(.75);
	filter: sepia(.22) brightness(1.1) contrast(.85) saturate(.75)
}

.reyes::after {
	background: #efcdad
}

.gingham {
	-webkit-filter: brightness(1.05) hue-rotate(-10deg);
	filter: brightness(1.05) hue-rotate(-10deg)
}

.gingham::after {
	background: -webkit-linear-gradient(left, rgba(66, 10, 14, .2), transparent);
	background: linear-gradient(to right, rgba(66, 10, 14, .2), transparent);
	mix-blend-mode: darken
}

.toaster {
	-webkit-filter: contrast(1.5) brightness(.9);
	filter: contrast(1.5) brightness(.9)
}

.toaster::after {
	background: -webkit-radial-gradient(circle, #804e0f, #3b003b);
	background: radial-gradient(circle, #804e0f, #3b003b);
	mix-blend-mode: screen
}

.walden {
	-webkit-filter: brightness(1.1) hue-rotate(-10deg) sepia(.3) saturate(1.6);
	filter: brightness(1.1) hue-rotate(-10deg) sepia(.3) saturate(1.6)
}

.walden::after {
	background: #04c;
	mix-blend-mode: screen;
	opacity: .3
}

.hudson {
	-webkit-filter: brightness(1.2) contrast(.9) saturate(1.1);
	filter: brightness(1.2) contrast(.9) saturate(1.1)
}

.hudson::after {
	background: -webkit-radial-gradient(circle, #a6b1ff 50%, #342134);
	background: radial-gradient(circle, #a6b1ff 50%, #342134);
	mix-blend-mode: multiply;
	opacity: .5
}

.earlybird {
	-webkit-filter: contrast(.9) sepia(.2);
	filter: contrast(.9) sepia(.2)
}

.earlybird::after {
	background: -webkit-radial-gradient(circle, #d0ba8e 20%, #360309 85%, #1d0210 100%);
	background: radial-gradient(circle, #d0ba8e 20%, #360309 85%, #1d0210 100%);
	mix-blend-mode: overlay
}

.mayfair {
	-webkit-filter: contrast(1.1) saturate(1.1);
	filter: contrast(1.1) saturate(1.1)
}

.mayfair::after {
	background: -webkit-radial-gradient(40% 40%, circle, rgba(255, 255, 255, .8), rgba(255, 200, 200, .6), #111 60%);
	background: radial-gradient(circle at 40% 40%, rgba(255, 255, 255, .8), rgba(255, 200, 200, .6), #111 60%);
	mix-blend-mode: overlay;
	opacity: .4
}

.lofi {
	-webkit-filter: saturate(1.1) contrast(1.5);
	filter: saturate(1.1) contrast(1.5)
}

.lofi::after {
	background: -webkit-radial-gradient(circle, transparent 70%, #222 150%);
	background: radial-gradient(circle, transparent 70%, #222 150%);
	mix-blend-mode: multiply
}

._1977 {
	-webkit-filter: contrast(1.1) brightness(1.1) saturate(1.3);
	filter: contrast(1.1) brightness(1.1) saturate(1.3)
}

._1977:after {
	background: rgba(243, 106, 188, .3);
	mix-blend-mode: screen
}

.brooklyn {
	-webkit-filter: contrast(.9) brightness(1.1);
	filter: contrast(.9) brightness(1.1)
}

.brooklyn::after {
	background: -webkit-radial-gradient(circle, rgba(168, 223, 193, .4) 70%, #c4b7c8);
	background: radial-gradient(circle, rgba(168, 223, 193, .4) 70%, #c4b7c8);
	mix-blend-mode: overlay
}

.xpro2 {
	-webkit-filter: sepia(.3);
	filter: sepia(.3)
}

.xpro2::after {
	background: -webkit-radial-gradient(circle, #e6e7e0 40%, rgba(43, 42, 161, .6) 110%);
	background: radial-gradient(circle, #e6e7e0 40%, rgba(43, 42, 161, .6) 110%);
	mix-blend-mode: color-burn
}

.nashville {
	-webkit-filter: sepia(.2) contrast(1.2) brightness(1.05) saturate(1.2);
	filter: sepia(.2) contrast(1.2) brightness(1.05) saturate(1.2)
}

.nashville::after {
	background: rgba(0, 70, 150, .4);
	mix-blend-mode: lighten
}

.nashville::before {
	background: rgba(247, 176, 153, .56);
	mix-blend-mode: darken
}

.lark {
	-webkit-filter: contrast(.9);
	filter: contrast(.9)
}

.lark::after {
	background: rgba(242, 242, 242, .8);
	mix-blend-mode: darken
}

.lark::before {
	background: #22253f;
	mix-blend-mode: color-dodge
}

.moon {
	-webkit-filter: grayscale(1) contrast(1.1) brightness(1.1);
	filter: grayscale(1) contrast(1.1) brightness(1.1)
}

.moon::before {
	background: #a0a0a0;
	mix-blend-mode: soft-light
}

.moon::after {
	background: #383838;
	mix-blend-mode: lighten
}

.clarendon {
	-webkit-filter: contrast(1.2) saturate(1.35);
	filter: contrast(1.2) saturate(1.35)
}

.clarendon:before {
	background: rgba(127, 187, 227, .2);
	mix-blend-mode: overlay
}

.willow {
	-webkit-filter: grayscale(.5) contrast(.95) brightness(.9);
	filter: grayscale(.5) contrast(.95) brightness(.9)
}

.willow::before {
	background-color: radial-gradient(40%, circle, #d4a9af 55%, #000 150%);
	mix-blend-mode: overlay
}

.willow::after {
	background-color: #d8cdcb;
	mix-blend-mode: color
}

.rise {
	-webkit-filter: brightness(1.05) sepia(.2) contrast(.9) saturate(.9);
	filter: brightness(1.05) sepia(.2) contrast(.9) saturate(.9)
}

.rise::after {
	background: -webkit-radial-gradient(circle, rgba(232, 197, 152, .8), transparent 90%);
	background: radial-gradient(circle, rgba(232, 197, 152, .8), transparent 90%);
	mix-blend-mode: overlay;
	opacity: .6
}

.rise::before {
	background: -webkit-radial-gradient(circle, rgba(236, 205, 169, .15) 55%, rgba(50, 30, 7, .4));
	background: radial-gradient(circle, rgba(236, 205, 169, .15) 55%, rgba(50, 30, 7, .4));
	mix-blend-mode: multiply
}

._1977:after, ._1977:before, .aden:after, .aden:before, .brooklyn:after, .brooklyn:before, .clarendon:after, .clarendon:before, .earlybird:after, .earlybird:before, .gingham:after, .gingham:before, .hudson:after, .hudson:before, .inkwell:after, .inkwell:before, .lark:after, .lark:before, .lofi:after, .lofi:before, .mayfair:after, .mayfair:before, .moon:after, .moon:before, .nashville:after, .nashville:before, .perpetua:after, .perpetua:before, .reyes:after, .reyes:before, .rise:after, .rise:before, .slumber:after, .slumber:before, .toaster:after, .toaster:before, .walden:after, .walden:before, .willow:after, .willow:before, .xpro2:after, .xpro2:before {
	content: '';
	display: block;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	position: absolute;
	pointer-events: none
}

._1977, .aden, .brooklyn, .clarendon, .earlybird, .gingham, .hudson, .inkwell, .lark, .lofi, .mayfair, .moon, .nashville, .perpetua, .reyes, .rise, .slumber, .toaster, .walden, .willow, .xpro2 {
	position: relative
}

._1977 img, .aden img, .brooklyn img, .clarendon img, .earlybird img, .gingham img, .hudson img, .inkwell img, .lark img, .lofi img, .mayfair img, .moon img, .nashville img, .perpetua img, .reyes img, .rise img, .slumber img, .toaster img, .walden img, .willow img, .xpro2 img {
	width: 100%;
	z-index: 1
}

._1977:before, .aden:before, .brooklyn:before, .clarendon:before, .earlybird:before, .gingham:before, .hudson:before, .inkwell:before, .lark:before, .lofi:before, .mayfair:before, .moon:before, .nashville:before, .perpetua:before, .reyes:before, .rise:before, .slumber:before, .toaster:before, .walden:before, .willow:before, .xpro2:before {
	z-index: 2
}

._1977:after, .aden:after, .brooklyn:after, .clarendon:after, .earlybird:after, .gingham:after, .hudson:after, .inkwell:after, .lark:after, .lofi:after, .mayfair:after, .moon:after, .nashville:after, .perpetua:after, .reyes:after, .rise:after, .slumber:after, .toaster:after, .walden:after, .willow:after, .xpro2:after {
	z-index: 3
}

.slumber {
	-webkit-filter: saturate(.66) brightness(1.05);
	filter: saturate(.66) brightness(1.05)
}

.slumber::after {
	background: rgba(125, 105, 24, .5);
	mix-blend-mode: soft-light
}

.slumber::before {
	background: rgba(69, 41, 12, .4);
	mix-blend-mode: lighten
}
